import React from 'react';

export default function ApplyLinks(props) {
    if (props.data) {
        // console.log('data: ' + props.data);
        return props.data.map((d, index) => {
            // console.log('btnText: ' + props.btnText);
            return (
                <li key={index} className="apply">
                    <a href={d['options']['apply']}>
                        <span>{props.btnText}</span>&nbsp;
                        <span
                            className="far fa-external-link"
                            aria-label="(external link)"
                        />
                    </a>
                </li>
            );
        });
    }
}
