import ApplyLinks from '../buttons/apply';
import React from 'react';

const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
];

export default function StartDates(props) {
    if (props.data) {
        // console.log('SD: ' + props.data + props.data.length);

        // Format date so it's possible to filter on based on button text value
        let data = props.data;
        let formattedDate;
        for (const d of data) {
            formattedDate = new Date(d.header);
            d.header = `${
                months[formattedDate.getUTCMonth()]
            } ${formattedDate.getUTCFullYear()}`;
        }

        return props.data.map((d, index) => {
            if (d['options'][0]['options'].length > 1) {
                // console.log('load methods screen');
                return (
                    <li key={index}>
                        <button
                            onClick={(e) => {
                                // Target selected date button's text value, e.g. 'September 2021'
                                let dateBtn = e.target.querySelector('span');
                                let dateVal = dateBtn.textContent;

                                // Filter entry points data on selected qualification
                                let filteredDateData = props.data.filter(
                                    (e) => e.header === dateVal
                                );

                                // Multiple methods of study? (Skipping Location for now)
                                for (const f of filteredDateData) {
                                    if (f['options'][0]['options'].length > 1) {
                                        // Open method of study selection
                                    } else {
                                        <ul className="how-to-apply--pgt--js__options">
                                            {
                                                <ApplyLinks
                                                    btnText={'abc'}
                                                    data={
                                                        f['options'][0][
                                                            'options'
                                                        ][0]['options']
                                                    }
                                                />
                                            }
                                        </ul>;
                                    }
                                }
                            }}
                        >
                            <span>{d.header}</span>
                        </button>
                    </li>
                );
            } else {
                // console.log([d['options'][0]['options'][0]]);
                return (
                    <ApplyLinks
                        btnText={d.header}
                        data={[d['options'][0]['options'][0]]}
                    />
                );
            }
        });
    }
}
